
import React, { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";

const PersistentTodoList = () => {
  const [todos, setTodos] = useState([]);
  const [newTodo, setNewTodo] = useState("");
  const [editing, setEditing] = useState({ id: null, text: "" });

  useEffect(() => {
    const storedTodos = localStorage.getItem("todos");
    if (storedTodos) setTodos(JSON.parse(storedTodos));
  }, []);

  useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos]);

  const addTodo = () => {
    if (newTodo.trim()) {
      setTodos([{ id: Date.now(), text: newTodo, completed: false }, ...todos]);
      setNewTodo("");
    }
  };

  const toggleComplete = (id) => {
    setTodos(todos.map(todo => todo.id === id ? { ...todo, completed: !todo.completed } : todo));
  };

  const deleteTodo = (id) => setTodos(todos.filter(todo => todo.id !== id));

  const clearAll = () => setTodos([]);

  const editTodo = (id, text) => setEditing({ id, text });

  const saveEdit = (id) => {
    setTodos(todos.map(todo => todo.id === id ? { ...todo, text: editing.text } : todo));
    setEditing({ id: null, text: "" });
  };

  return (
    <div
      className="min-h-screen flex flex-col items-center pt-10 bg-cover bg-center"
      style={{ backgroundImage: "url(/images/background.jpg)" }}
    >
      <div className="bg-white/70 p-6 rounded-lg shadow-lg w-full max-w-xl">
        <h1 className="text-3xl font-bold text-center mb-4">To-Do List</h1>
        <div className="flex mb-4">
          <input
            type="text"
            value={newTodo}
            onChange={(e) => setNewTodo(e.target.value)}
            placeholder="Add a task..."
            className="flex-1 p-2 border rounded-l"
          />
          <button onClick={addTodo} className="p-2 bg-blue-500 text-white rounded-r">Add</button>
        </div>
        <ul>
          <AnimatePresence>
            {todos.map(todo => (
              <motion.li
                key={todo.id}
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -10 }}
                className={`p-2 flex items-center justify-between border-b ${todo.completed ? "bg-green-100" : "bg-white"}`}
              >
                <input
                  type="checkbox"
                  checked={todo.completed}
                  onChange={() => toggleComplete(todo.id)}
                  className="mr-2"
                />
                {editing.id === todo.id ? (
                  <input
                    type="text"
                    value={editing.text}
                    onChange={(e) => setEditing({ ...editing, text: e.target.value })}
                    className="flex-1 mr-2 border p-1"
                  />
                ) : (
                  <span className={`flex-1 ${todo.completed ? "line-through text-gray-500" : ""}`}>{todo.text}</span>
                )}
                {editing.id === todo.id ? (
                  <button onClick={() => saveEdit(todo.id)} className="text-green-500">Save</button>
                ) : (
                  <button onClick={() => editTodo(todo.id, todo.text)} className="text-blue-500">Edit</button>
                )}
                <button onClick={() => deleteTodo(todo.id)} className="text-red-500 ml-2">Delete</button>
              </motion.li>
            ))}
          </AnimatePresence>
        </ul>
        {todos.length > 0 && (
          <button onClick={clearAll} className="mt-4 w-full bg-red-500 text-white p-2 rounded">Clear All</button>
        )}
      </div>
    </div>
  );
};

export default PersistentTodoList;
